/* --------------------
   Our Work Section
   --------------------- */
.spacer {
  height: 5vw;
}

.grid li {
  padding: 15px;
  height: 350px;
  width: 350px;
  border-radius: 3px;
  background-clip: content-box;
  background-size: cover;
  background-position: center;
  background-color: #333;
}

.grid li.large {
  height: 500px;
  width: 500px;
}

.grid li.small {
  -ms-flex-preferred-size: 30%;
      flex-basis: 30%;
}
.grid li.large {
  -ms-flex-preferred-size: 45%;
      flex-basis: 45%;
}

.Gallery-Blurb {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  width: 65vw;
  border-bottom: 1px solid #51504c;
  margin: 0 auto;
}
.Gallery-Blurb p {
  max-width: 60vw;
  margin: 0 auto;
  font-weight: 300;
  font-size: 20px;
  margin-bottom: 20px;
}

@media (max-width: 1000px) {
  .grid li.small,
  .our-work .grid li.large {
    -ms-flex-preferred-size: 100%;
        flex-basis: 100%;
  }
}



/*about page*/
.About-Staff-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  background-color: #b2a693;
}

.Staff-Header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  width: 100%;
  -ms-flex-pack: center;
      justify-content: center;
}

.Staff-Bios {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.Staff-Card {
  background-color: #d9cab3;
  border: 1px solid #60574a;
  -webkit-box-shadow: 3px 3px 3px #635c52;
          box-shadow: 3px 3px 3px #635c52;
  color: #60574a;
  width: 55vw;
  margin: 1.5vw;
  border-radius: 10px;
}

.Staff-Card h3 {
  margin-bottom: 8px;
}
.Staff-Card h3 span {
  padding: 1px 5px;
  border-radius: 5px;
  border-bottom: 1px solid black;
  background-color: rgba(255, 255, 255, 0.5);
}

.Staff-Card div:first-child {
  float: left;
}

.Staff-Card div:first-child div {
  padding: 12px;
  height: 25vw;
  width: 20vw;
  border-radius: 7px;
  margin: 1vw;
  -webkit-box-shadow: 3px 3px 6px grey;
          box-shadow: 3px 3px 6px grey;
  background-size: cover;
}

.Staff-Card h2 {
  display: block;
  float: right;
  width: 29vw;
  text-align: left;
  margin: 1.25vw 0;
  padding-left: 2vw;
  white-space: nowrap;
}


.Staff-Card p {
  margin: 0.5vw 5%;
}

@media( min-width: 860px ){
  .Staff-Card h2 {
    width: 32vw;
    padding: 0;
    text-align: center;
  }

  .Staff-Card p {
    margin: 0.5vw 12% 0.5vw 50%;
  }
}

@media( max-width: 560px ){
  .Staff-Card h2 {
    font-size: 0.875em;
    padding: 0;
    width: 27vw;
  }

  .Staff-Card h3 {
    font-size: 1em;

  }
}



.pageBottom > div:first-child {
  background-size: cover;
}



