.foot-logo {
  height: 100px;
}

.foot-logo:hover path {
  stroke: #e8e;
  fill: purple;
}

.foot-logo:hover path:nth-child(1) {
  -webkit-animation: spin 1 1.25s linear reverse;
          animation: spin 1 1.25s linear reverse;
  -webkit-transform-origin: -1780px 34px;
      -ms-transform-origin: -1780px 34px;
          transform-origin: -1780px 34px;
}

.foot-logo:hover path:nth-child(2) {
  -webkit-animation: spin 1 1.375s linear reverse;
          animation: spin 1 1.375s linear reverse;
  -webkit-transform-origin: -1770px 34px;
      -ms-transform-origin: -1770px 34px;
          transform-origin: -1770px 34px;
}

.foot-logo:hover path:nth-child(3) {
  -webkit-animation: spin 1 1.5s linear reverse;
          animation: spin 1 1.5s linear reverse;
  -webkit-transform-origin: -1760px 34px;
      -ms-transform-origin: -1760px 34px;
          transform-origin: -1760px 34px;
}

.foot-logo:hover path:nth-child(4) {
  -webkit-animation: spin 1 1.625s linear reverse;
          animation: spin 1 1.625s linear reverse;
  -webkit-transform-origin: -1750px 34px;
      -ms-transform-origin: -1750px 34px;
          transform-origin: -1750px 34px;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg) scale(1, 1); transform: rotate(0deg) scale(1, 1) }
  50% { -webkit-transform: rotate(-180deg) scale(2, 2); transform: rotate(-180deg) scale(2, 2) }
  100% { -webkit-transform: rotate(-360deg) scale(1, 1); transform: rotate(-360deg) scale(1, 1) }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg) scale(1, 1); transform: rotate(0deg) scale(1, 1) }
  50% { -webkit-transform: rotate(-180deg) scale(2, 2); transform: rotate(-180deg) scale(2, 2) }
  100% { -webkit-transform: rotate(-360deg) scale(1, 1); transform: rotate(-360deg) scale(1, 1) }
}



/* this is on the div generated by the LazyHero to create a transition color*/
.hLWXCW {
  background: url("./imgs/woodHeader.jpg");
  display: -ms-flexbox;
  display: flex;
  height: 20vh;
  width: 100%;
  position: relative;
  background-size: cover;
}

.hLWXCW:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;

  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.65);
}



