.contact-section-tag {
  background-color: #d9cab3;
}

.contact-container input {
  border-right: none;
  border-left: none;
  border-top: none;
  border-color: black;
}

.page {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

#name {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  border-color: black;
}

textarea {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  border-color: black;
}

.form-container {
  width: 76.16vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;

  color: grey;
  margin: 0 auto;
  -webkit-box-shadow: 2px 2px 12px 1px black;
          box-shadow: 2px 2px 12px 1px black;
  height: 76vh;
}

.form-container:active {
  color: #b3aca7;
}

.form-header {
  width: 71vw;
  color: black;
  margin: 10px auto 0;
  letter-spacing: 5px;
  font-family: "Oswald", sans-serif;
  font-weight: 700;
  font-size: 5vh;
  text-shadow: 1px 1px rgba(500, 500, 500, 0.3);
}

#form {
  width: 500px;
  margin: 23px auto 100px auto;
}

#form input {
  background-color: rgba(50, 50, 50, 0.3);
}

#form input:nth-child(4) {
  background-color: rgba(50, 50, 50, 0.3);
  border: 1px solid black;
}

#form input:hover {
  background: rgba(0, 0, 0, 0.45);
}

#form input:nth-child(4):hover {
  color: rgb(215, 215, 215);
  background-color: rgba(100, 100, 100, 0.2);
}

.contact-container input {
  font-family: sans-serif;
  font-size: 0.875em;
  width: 470px;
  height: 50px;
  padding: 0px 15px 0px 15px;
  background: transparent;
  outline: none;
  color: white;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

contact-container input:hover {
  background: #dddddd;
}

.contact-container textarea {
  width: 470px;
  max-width: 470px;
  background: rgba(50, 50, 50, 0.3);
  height: 110px;
  max-height: 110px;
  padding: 15px;
  outline: none;
  color: white;
  font-family: sans-serif;
  font-size: 0.875em;
  border-right: none;
  border-left: none;
  border-bottom: none;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  border-color: black;
}

.contact-container textarea:hover {
  background: rgba(0, 0, 0, 0.45);
}

#submit {
  width: 502px;
  padding: 0;
  margin: 16px 0px 0px 0px;
  font-family: "Oswald", sans-serif;
  font-size: 0.875em;
  outline: none;
  cursor: pointer;
  color: black;
  -webkit-box-shadow: 2px 2px 6px 1px black;
          box-shadow: 2px 2px 6px 1px black;
  background-color: rgba(100, 100, 100, 0.4);
}

#submit:hover {
  -webkit-box-shadow: none;
          box-shadow: none;
}

@media (max-width: 1050px) {
  #submit {
    max-width: 350px;
  }
}

@media (max-width: 650px) {
  .contact-container input {
    width: 300px;
  }
  .contact-container textarea {
    width: 300px;
  }

  #submit {
    width: 300px;
  }
}

@media (max-width: 566px) {
  .contact-container input {
    width: 250px;
  }
  .contact-container textarea {
    width: 250px;
  }

  #submit {
    width: 250px;
  }
}

.emailInvalid {
  height: 0;
}

.emailRetry {
  float: left;
  color: #a93f55;
  font-weight: 600;
  margin: 0 auto;
  width: 76vw;
  height: 50px;
  background: transparent;
  outline: none;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.textarea-counter {
  position: absolute;
  right: 3vw;
  bottom: 1vh;
}

.contact-email-div img {
  position: absolute;
  height: 5vh;
  left: 64.8vw;
  top: 48vh;
}

@media (max-width: 503px) {
  .contact-container input {
    width: 250px;
  }
  textarea {
    width: 250px;
  }

  #submit {
    width: 250px;
  }
  .textarea-counter {
    -webkit-transform: translateX(-100%);
        -ms-transform: translateX(-100%);
            transform: translateX(-100%);
    display: block;
    right: 0;
    bottom: 1vh;
  }
}

.textarea-wrapper {
  position: relative;
}

.textarea-wraper span {
  position: absolute;
}

.more-info-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 0 10%;
  padding-bottom: 20px;
}

.location,
.parking,
.policy {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
  width: 50%;
  padding: 20px;
  text-align: left;
}

.map {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  width: 50%;

  margin-bottom: 20px;
  padding-top: 50%;
  background-size: cover;
}

@media (max-width: 600px) {
  .more-info-container {
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .map,
  .parking,
  .policy {
    width: 80%;
  }
  .map {
    background-position: 0 -60px;
  }
}



